import React from 'react';

const SearchBox = (props) => {
    return (
        <div className="col col-sm-8 ">
            <input className="form-control align-items-end" value={props.value} onChange={(event) => props.setSearchValue(event.target.value)} placeholder="Search for movies!"></input>
        </div>
    )
}

export default SearchBox;